import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {MessagesService} from '../messages.service';
import {ButtonsService} from '../buttons.service';

@Component({
  selector: 'app-mass',
  templateUrl: './mass.component.html',
  styleUrls: ['./mass.component.css']
})
export class MassComponent implements OnInit {

  messages: any;
  mass: any = {
    message: '',
    buttons: [],
    type: 1,
    query: null
  }

  constructor(private route: ActivatedRoute,
              private router: Router,
              private messagesService: MessagesService,
              private buttonsService: ButtonsService) {
  }

  ngOnInit() {
    this.getAllMessages();
  }

  getAllMessages() {
    this.messagesService.getAllQuestions().subscribe((res: any) => {
      this.messages = res;
    });
  }

  sendMass() {
    this.messagesService.sendMass(this.mass).subscribe((data: any) => {
      alert('Sending mass message');
    });
  }

  addButton() {
    if (this.mass.buttons.length < 9) {
      this.mass.buttons.push({
        ID: this.mass.buttons.length + 1,
        next: null,
        type: null,
        name: ''
      });
    } else {
      alert('FB allows only 9 buttons');
    }
  }

  typeChange() {
    this.mass.type = Number(this.mass.type)
    console.log(this.mass);
  }

  queryChange() {
    if (this.mass.query === '') {
      this.mass.query = null;
    }
  }
}



