import {Component, OnInit} from '@angular/core';
import {MessagesService} from '../messages.service';

@Component({
  selector: 'app-reporting',
  templateUrl: './reporting.component.html',
  styleUrls: ['./reporting.component.css']
})
export class ReportingComponent implements OnInit {

  total: any = [];
  reports: any = [];

  constructor(private messagesSerive: MessagesService) {
  }

  ngOnInit() {
    this.messagesSerive.getReportsTotal().subscribe((data: any) => {
      this.total = data;
    });

    this.messagesSerive.getAllReports().subscribe((data: any) => {
      this.reports = data;
    });
  }

  getNumberForStep(id, step) {
    step = Number(step)
    let num = this.reports.filter(o => {
      return o.id === id;
    })[0]
      .data
      .filter(n => {
      return n.step === step;
    })[0];
    console.log(num);
    return num.count + '/' + num.f;
  }

}



