import {ModuleWithProviders} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

import {ButtonsComponent} from './buttons/buttons.component';
import {ButtonsAddComponent} from './buttons-add/buttons-add.component';
import {MessagesListComponent} from './messages-list/messages-list.component';
import {MessageAddComponent} from './message-add/message-add.component';
import {MassComponent} from './mass/mass.component';
import {AuthGuard} from './auth.guard';
import {LoginComponent} from './login/login.component';
import {ReportingComponent} from './reporting/reporting.component';

export const router: Routes = [
  {path: '', component: MessagesListComponent, canActivate: [AuthGuard]},
  {path: 'messages-add', component: MessageAddComponent, canActivate: [AuthGuard]},
  {path: 'messages-update/:id', component: MessageAddComponent, canActivate: [AuthGuard]},
  {path: 'buttons', component: ButtonsComponent, canActivate: [AuthGuard]},
  {path: 'buttons-update/:id', component: ButtonsAddComponent, canActivate: [AuthGuard]},
  {path: 'buttons-add', component: ButtonsAddComponent, canActivate: [AuthGuard]},
  {path: 'mass', component: MassComponent, canActivate: [AuthGuard]},
  {path: 'login', component: LoginComponent},
  {path: 'reporting', component: ReportingComponent}
];

export const routes: ModuleWithProviders = RouterModule.forRoot(router);



